import * as React from "react";
import { pipe, chunk, createBlob } from "@atayahmet/blobify";
import { IKeyValueObject } from '@music/interfaces';

interface IFakeVisualizerProps {
  unit: "percent" | "second";
  duration?: number;
  elapsed?: number;
  percent?: number;
}

interface IPipe {
  height: number;
}

export default class FakeVisualizer extends React.Component<
  IFakeVisualizerProps,
  any
> {
  public state = {
    pipes: [],
    blobUrl: null,
    percent: 0,
    lastRefresh: 1,
    width: window.innerWidth
  };
  public el: HTMLDivElement|null;

  private get width(): number {
    return this.el ? ((this.el as HTMLDivElement).offsetWidth || 0) : 0;
  }

  private get pipes(): IPipe[] {
    return this.state.pipes || [];
  }

  public async componentDidMount() {
    try {
      window.onresize = this.resizeHandler;
      const parameters = await this.getParameters();
      this.setState({ ...parameters, ...this.props });
    } catch (e) {
      console.error(e);
    }
  }

  public componentDidUpdate(prevProps: IFakeVisualizerProps) {
    if (((this.props.percent || 0) - (this.state.lastRefresh || 0)) > .3) {
      this.setState({ ...this.props, lastRefresh: this.props.percent });
    }
  }

  public render() {
    return (
      <>
        <div className="FakeVisualizer" ref={e => this.el = e}>
          {this.pipes.map((pipe: IPipe, index: number) => (
            <div
              key={index}
              onClick={this.clickHandler(index)}
              onMouseOver={this.mouseOverHandler}
              onMouseLeave={this.mouseLeaveHandler}
              style={{
                ...(() =>
                  this.calcPercent(index + 1) < this.state.percent
                    ? { backgroundColor: "red" }
                    : {})(),
                ...pipe
              }}
              className="pipe"
            />
          ))}
        </div>
      </>
    );
  }

  private resizeHandler = (e: any) => {
    if (this.width !== this.state.width) {
      const parameters = this.populate();
      this.setState({ ...parameters }, () => this.createBlobUrl(parameters));
    }
  };

  private createPipes(): IPipe[] {
    const dividing = 5;
    const width = this.width + Math.ceil(this.width / dividing);
    const pipes = width / dividing;
    const pipeCount = Math.ceil(pipes) + Math.ceil(dividing / this.width * pipes * 100);
    return Array(pipeCount)
      .fill(1)
      .map(() => ({ height: 50 + Math.ceil(Math.random() * 20) }));
  }

  private createBlobUrl(parameters: IKeyValueObject) {
    pipe(() => JSON.stringify(parameters))
      .pipe(stream => chunk({ chars: stream, slice: 128 }))
      .pipe(chunks => createBlob(chunks, "plain/text"))
      .pipe(blob => URL.createObjectURL(blob))
      .catch(console.error)
      .run()
      .then((blobUrl: string) => this.setState({ blobUrl }));
  }

  private getParameters(): Promise<any> {
    return fetch(this.state.blobUrl || "")
      .then(res => res.json())
      .catch(this.catchHandler);
  }

  private catchHandler = (e: Error) => {
    const parameters = this.populate();
    this.createBlobUrl(parameters);
    return parameters;
  };

  private populate() {
    const pipes = this.createPipes();
    const width = this.width;
    return {
      pipes,
      width
    };
  }

  private calcPercent(current: number): number {
    return Math.ceil((current / this.pipes.length) * 100);
  }

  private mouseOverHandler = (e: React.MouseEvent) => {
    e.persist();
    const el = { ...e };
    el.currentTarget.setAttribute(
      "data-color",
      el.target["style"].backgroundColor
    );
    el.target["style"].backgroundColor = "black";
    el.target["style"].cursor = "pointer";
  };

  private mouseLeaveHandler = (e: React.MouseEvent) => {
    e.persist();
    const el = { ...e };
    el.target["style"].padding = "0px";
    el.target["style"].backgroundColor = el.currentTarget.getAttribute(
      "data-color"
    );
  };

  private clickHandler = (index: number) => {
    return (e: React.MouseEvent) => {
      e.persist();
      const el = { ...e };
      el.currentTarget.setAttribute("data-color", "#17d0d2");
      this.setState({ percent: this.calcPercent(index + 3) });
    };
  };
}



